import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import Grid from '@material-ui/core/Grid';

import Footer from '../footer/footer';
import '../fonts/fonts.css'

const useStyles = makeStyles({
	root: {
		maxWidth: 220,
	},
	minWidth: {
		width: '100%',
	},
	titleH6: {
		lineHeight: 1.2,
	},
	marginCard: {
		margin: '0 0 8px 0',
	}
});



const CustomCard = ({ cards }) => {

	const classes = useStyles();

	const elements = cards.map((item) => {
	
		return (
			<Grid key={item.id} item xs={6} sm={3} lg={2} className={classes.root}>
				<Card className={classes.minWidth, classes.marginCard}>
					<Box display="flex" justifyContent="center" p={1}>
						<div className={item.cardClass}></div>
					</Box>
					<Box p={1}>
						<Typography className={classes.titleH6} align="center" variant="h6" component="h2">
							{item.title}
						</Typography>
					</Box>
				</Card>
				<Button className={classes.minWidth} color="primary" variant="contained" href={item.cpUrl}>
					SVG on Codepen
				</Button>
			</Grid>
		);
	  })
	
    return (
		<Grid container spacing={2} justify="center">
			{ elements }
			<Grid item xs={12}>
				<Footer />
			</Grid>
		</Grid>
    );
  }

export default CustomCard;